import { useEffect } from "react"
import { useState } from "react"

const useProducts = () => {
  const [products, setProducts] = useState([])
  useEffect(function () {
    fetch("./products.json")
      .then((res) => res.json())
      .then((data) => setProducts(data))
  }, [])
  return [products]
}

export default useProducts
